export default {
	mounted(){ 
		const getter = this.$store.getters; 
		const activeTab = getter.getTabActive;
		$(".tab_menu ul li").removeClass("active");
		$(".tab_menu ul li#"+activeTab).addClass("active"); 
	},
	props:{ 
      placeGroupList:{
          type:Array,
          required:false
      }
	},
	methods:{
		activeTab(tab){ 
			this.$store.dispatch("activeTab", tab)
		}
	}
}
